.card-emprestimo{width:100%;height:7rem;position:relative;cursor:pointer;border-radius:.5rem}.card-emprestimo .hover{width:100%;height:100%;position:absolute;background:transparent;border-radius:.5rem;z-index:4;transition:all .2s ease-out}.card-emprestimo:hover .hover{box-shadow:0 14px 28px rgba(0,0,0,.25),0 10px 10px rgba(0,0,0,.22)}.card-emprestimo:hover h3{font-size:1.2rem}.card-emprestimo .cover{width:100%;height:100%;position:absolute;background:linear-gradient(45deg, rgba(0, 0, 0, 0.616), rgba(0, 0, 0, 0));border-radius:.5rem;z-index:2}.card-emprestimo img[alt="PENSIONISTAS INSS"]{object-position:left 30%}.card-emprestimo img[alt="SERVIDORES PÚBLICOS"]{object-position:left 30%}.card-emprestimo img[alt="FORÇAS ARMADAS"]{object-position:center left}.card-emprestimo img[alt="CARTÃO DE CRÉDITO CONSIGNADO"]{object-position:center left}.card-emprestimo img[alt="PORTABILIDADE DE CRÉDITO"]{object-position:center left}.card-emprestimo img{width:100%;height:100%;position:absolute;object-fit:cover;object-position:top left;z-index:1;border-radius:.5rem}.card-emprestimo h3{transition:all .2s ease-out;display:flex;justify-content:flex-end;position:absolute;padding:1rem;bottom:0;left:0;color:#fff;z-index:3}